import React from 'react';
import ReactDOM from 'react-dom';
import App from './Components/App';
import {BrowserRouter, Switch} from 'react-router-dom';


ReactDOM.render(
    <BrowserRouter>
        <Switch>
            <App />
        </Switch>
    </BrowserRouter>,
    document.getElementById('root')
);


